body {
  margin: 0%;
}
.Header-wrap {
  display: flex;
  background-color: rgba(0, 0, 0, 0.815);
  align-items: center;
  justify-content: space-around;
  color: white;
  flex-wrap: wrap;
}
.One {
  /* width: 33.3vw; */
  display: none;
  /* box-shadow: 0px 2px  4px black; */
}
img {
  /* margin-left: 10px; */
  width: 25px;
}
.Two {
  /* width: 33.3vw; */
  /* box-shadow: 0px 2px  4px black; */
  text-align: center;
}

h1 {
  margin: 0px;
  margin-left: 0px;
  padding-bottom: 5px;
  font-weight: 300;
  font-size: 30px;
}
.Three {
  /* width: 20.3vw; */
  /* box-shadow: 0px 2px  4px black; */
  text-align: center;
}
ul {
  /* margin: 0px; */
  padding: 5px;
  padding-bottom: 8px;
  border-radius: 20px;
  list-style: none;
  background-color: rgba(0, 0, 0, 0.396);
}
ul li {
  display: inline;
  transition: 0.5s;
  margin: 0px 20px 0px 20px;
}
ul li:hover {
    cursor: pointer;
    text-shadow: 0px 0px 8px rgb(0, 255, 81);
    border-bottom: 1px solid rgb(204, 255, 0);
}

@media (min-width: 280px) and (max-width: 425px) {
  .Header-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }
  .One {
    width: 25px;
    /* box-shadow: 0px 2px  4px black; */
  }
  img {
    width: 25px;
  }
  .Two {
    width: fit-content;
    margin-left: 40px;
    text-align: center;
  }

  h1 {
    font-weight: 300;
    font-size: 20px;
  }
  .Three {
    width: fit-content;
    text-align: center;
    margin-right: 10px;
  }
  ul {
    display: flex;
    padding: 0px;
    justify-content: center;
    width: 140px;
    list-style: none;
  }
  ul li {
    font-size: 10px;
    display: inline;
    padding: 5px;

    margin: 0px 5px 0px 5px;
  }
}
